import * as _ from "lodash";
export function validateAll(fields) {
    return new Promise((resolve, reject) => {
        Promise.all(fields.map((field) => field.validate().then(() => [], (reason) => [reason])))
            .then((errors) => _.flatMap(errors, (error) => error))
            .then((errors) => _.filter(errors, (error) => !_.isEmpty(error.errors)))
            .then((errors) => {
            if (!_.isEmpty(errors)) {
                reject(errors);
            }
            else {
                resolve();
            }
        });
    });
}
export function validateSequence(fields) {
    return new Promise((resolve, reject) => {
        fields
            .reduce((promise, field) => promise.then((errors) => {
            if (_.isEmpty(errors)) {
                return field.validate().then(() => [], (error) => [error]);
            }
            return errors;
        }), Promise.resolve([]))
            .then((errors) => {
            if (_.isEmpty(errors)) {
                resolve();
            }
            else {
                reject(errors);
            }
        });
    });
}
